html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

body {
  --another-color: #00acdc;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  display: flex;
}

.header__nav-list {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  margin-left: 20px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active {
  color: var(--another-color);
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: var(--another-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.header:hover {
  border: 2px solid var(--another-color);
}

.header:target {
  border: 1px solid var(--another-color);
}

.nav__link:hover {
  text-decoration: underline;
  -webkit-text-decoration-color: var(--another-color);
  text-decoration-color: var(--another-color);
}
